import React from 'react'
import * as d3 from 'd3'
import { schemeSet3 } from 'd3-scale-chromatic'
import Animate from 'react-move/Animate'
import NodeGroup from 'react-move/NodeGroup'
import * as R from 'ramda'

import Worker from './ForceSimulation.worker'
import style from './CharCooccurrence.css'

export default class CharCooccurrence extends React.Component {
  state = {
    loading: 0,
    nodes: [],
    links: [],
    colorScale: d3.scaleOrdinal(schemeSet3),
    linkOpacityScale: d3.scaleLinear(),
    nodeSizeScale: d3.scaleLinear(),
    transform: 'translate(0,0) scale(1)',
  }

  componentDidMount() {
    this.worker = new Worker()
    this.worker.onmessage = this.onmessage
  }

  onmessage = event => {
    const {
      data: { type, payload },
    } = event

    switch (type) {
      case 'TICK':
        this.setState({
          loading: payload,
        })
        break
      case 'END':
        this.setState(
          {
            loading: 100,
            nodes: payload.nodes,
            links: payload.links,
            nodeSizeScale: d3
              .scaleLinear()
              .range([5, 30])
              .domain(d3.extent(payload.nodes.map(d => d.numLinksTo))),
            linkOpacityScale: d3
              .scaleLinear()
              .range([0.1, 1])
              .domain(d3.extent(payload.links.map(d => d.value))),
          },
          () => {
            const bbox = this.nodesDom.getBBox()
            const bounds = [
              [bbox.x, bbox.y],
              [bbox.x + bbox.width, bbox.y + bbox.height],
            ]
            const [[x0, y0], [x1, y1]] = bounds
            const width = 960
            const height = 500
            const dx = x1 - x0
            const dy = y1 - y0
            const x = (x0 + x1) / 2
            const y = (y0 + y1) / 2
            const scale = 0.9 / Math.max(dx / width, dy / height)
            const translate = [width / 2 - scale * x, height / 2 - scale * y]
            if (
              translate !== this.state.translate ||
              scale !== this.state.scale
            ) {
              this.setState({
                transform: `translate(${translate.join(',')}) scale(${scale})`,
              })
            }
          },
        )
        break
    }
  }

  componentDidUpdate(prevProps) {
    const {
      data: { nodes, links },
    } = this.props
    const {
      data: { nodes: prevNodes, links: prevLinks },
    } = prevProps

    if (
      prevNodes.length === nodes.length &&
      prevLinks.length === links.length
    ) {
      return
    }

    console.log('didupdate')

    this.worker.postMessage({
      type: 'start',
      payload: {
        nodes,
        links,
      },
    })
  }

  render() {
    const {
      nodes,
      links,
      nodeSizeScale,
      linkOpacityScale,
      colorScale,
      transform,
      loading,
    } = this.state

    console.log('render')

    // if (loading < 100) {
    //   return <div>Loading: {loading}</div>
    // }

    // console.log('reder', R.groupBy(l => `${l.source.id}-${l.target.id}`)(links))

    return (
      <React.Fragment>
        <div>Loading: {loading}</div>
        <svg height={500} width="960">
          <Animate
            start={{
              transform: 'translate(0,0) scale(1)',
            }}
            update={{
              transform: [transform],
              timing: { duration: 750, delay: 250 },
            }}
          >
            {({ transform }) => (
              <g className={style.viewport} transform={transform}>
                <g className={style.links}>
                  {links.map(({ source, target, value }) => (
                    <line
                      x1={source.x}
                      y1={source.y}
                      x2={target.x}
                      y2={target.y}
                      strokeOpacity={linkOpacityScale(value)}
                      key={`${source.id}-${target.id}`}
                    />
                  ))}
                </g>
                <g className={style.nodes} ref={el => (this.nodesDom = el)}>
                  {nodes.map(node => (
                    <circle
                      cx={node.x}
                      cy={node.y}
                      fill={colorScale(node.index)}
                      r={nodeSizeScale(node.numLinksTo)}
                      key={node.index}
                    />
                  ))}
                  {/* <NodeGroup
                  data={nodes}
                  keyAccessor={d => d.index}
                  start={d => ({
                    cx: d.x,
                    cy: d.y,
                    fill: colorScale(d.index),
                    r: 0,
                  })}
                  enter={d => ({
                    r: [nodeSizeScale(d.numLinksTo)],
                  })}
                  update={d => ({
                    cx: [d.x],
                    cy: [d.y],
                    fill: [colorScale(d.index)],
                    r: [nodeSizeScale(d.numLinksTo)],
                  })}
                  exit={d => ({
                    r: [0],
                  })}
                >
                  {nodes => (
                    <React.Fragment>
                      {nodes.map((node, key) => (
                        <circle {...node.state} key={key} />
                      ))}
                    </React.Fragment>
                  )}
                </NodeGroup> */}
                </g>
              </g>
            )}
          </Animate>
        </svg>
      </React.Fragment>
    )
  }
}
